import React, { useEffect, useState, PureComponent, useContext } from "react";
import LineIcon from "react-lineicons";
import Image from "../components/image";
import ImageV2 from "./image-v2";
import { AppContext } from "./main";
import { DisplayOptionList } from "./image-options";

export function ImageList() {

  const { state, dispatch } = useContext(AppContext);

  const list = () => {
    let list = state.files.map((n, i) => {
      if (n)
        return <Item key={n.id} index={i} />
    })
    if (list.length <= 0) {
      list = 'нет фото';
    }

    return (
      <div className="list mt-3" change-mod={state.changeMod ? 1 : 0}>
        {list}
      </div>
    )

  }

  return list();
}

function Item(props) {

  const { state, dispatch } = useContext(AppContext);

  let n = state.files[props.index];
  let o = n.options;

  const rotateAspect = () => {
    dispatch({
      type: "rotate", payload: {
        index: props.index,
        r: !o.rotate
      }
    });
  }

  const changeIT = () => {
    if (state.changeMod) {
      o.changed = !o.changed;
      dispatch({
        type: "updateOptions", payload: {
          index: props.index,
          options: o
        }
      });
    }
  }

  if (o === undefined) return null;

  return (
    <div onClick={changeIT} className="file d-flex">
      <ImageV2 index={props.index} />
      <div className="file-options">
        <DisplayOptionList index={props.index} o={o} />
        <div className="delimeter"></div>
        <ItemOptions
          index={props.index}
          remove={() => dispatch({ type: "remove", payload: props.index })}
          rotate={rotateAspect} />
      </div>
      {o.changed && <div className="redact">Редактируется</div>}
    </div>
  )
}

function ItemOptions(props) {

  const { state, dispatch } = useContext(AppContext);

  return (
    <div className="item-option-list">
      <ItemOptionListBtn
        actionFunction={props.rotate}
        iconClass="lni lni-spinner-arrow"
        itemClass="rotate"
        index={props.index}
      />
      <ItemOptionListBtn
        actionFunction={() => dispatch({
          type: "remove",
          payload: props.index,
        })}
        iconClass="lni lni-close"
        itemClass="close"
        index={props.index}
      />
    </div>
  )
}

export function ItemOptionListBtn(props) {
  const { actionFunction, iconClass, itemClass } = { ...props }
  return (
    <div onClick={actionFunction} className={`item-option ${itemClass}`}><LineIcon name={iconClass} /></div>
  )
}

export default ImageList;