import React, { PureComponent } from "react";
import ReactCrop from "react-image-crop";
import { AppContext } from "./main";

class Image extends PureComponent {

  state = {
    src: '',
    image: {
      width: 0,
      height: 0
    },
    crop: {
      unit: '%',
      width: '100',
      aspect: 10 / 15,
      x: 0,
      y: 0,
    }
  }
  save = this.save.bind(this);

  componentDidMount() {
    const f = this.props.file;
    const src = f.options.cropped.length > 0 ? f.options.cropped[f.options.cropped.length - 1] : window.URL.createObjectURL(this.props.file.file);
    this.setState({ src });
  }

  componentDidUpdate() {
    this.createCROP();
  }

  componentWillUnmount() {
    const f = this.props.file;
    const src = f.options.cropped ? f.options.cropped : window.URL.createObjectURL(this.props.file.file);
    this.setState({ src: src });
  }

  createCROP() {
    let [aspect, width, height] = [
      this.props.file.options.aspect,
      this.state.image.width,
      this.state.image.height,
    ];
    let crop = this.state.crop;
    if (aspect !== undefined && aspect !== crop.aspect) {
      if (aspect > 1) {
        height = width / aspect;
      }
      else {
        width = height * aspect;
      }

      crop = {
        unit: 'px',
        aspect: aspect,
        width: width,
        height: height,
        x: 0,
        y: 0
      };
      this.setState({ crop });
    }
  }

  onImageLoaded = (image) => {
    this.imageRef = image;
    // this.(image);
    this.setState({
      image: { width: image.width, height: image.height }
    })
    this.createCROP();
  };

  onCropComplete = (crop) => {
    this.makeClientCrop(crop);
  };

  onCropChange = (crop, percentCrop) => {
    // You could also use percentCrop:
    // this.setState({ crop: percentCrop }); // в процентах
    this.setState({ crop }); // в пикселях
  };

  async makeClientCrop(crop) {
    if (this.imageRef && crop.width && crop.height) {
      const croppedImageUrl = await this.getCroppedImg(
        this.imageRef,
        crop,
        'newFile.jpeg'
      );
      this.setState({ croppedImageUrl });
      const o = this.props.file.options;
      o.preCrop = croppedImageUrl;
      this.props.d({
        type: "updateOptions", payload: {
          index: this.props.index,
          options: o
        }
      });
    }
  }

  async imageTo64(image) {
    if (image) {
      let o = this.props.s.files[this.props.index].options;
      let cropped = await this.getImage(image);
      o.cropped.push(cropped);
      this.props.d({
        type: "updateOptions", payload: {
          index: this.props.index,
          options: o
        }
      });
      this.setState({ src: cropped })
    }
  }

  getImage(image) {
    const canvas = document.createElement('canvas');
    const pixelRatio = window.devicePixelRatio;
    const scaleX = image.naturalWidth;
    const scaleY = image.naturalHeight;
    const ctx = canvas.getContext('2d');

    canvas.width = pixelRatio * scaleX;
    canvas.height = pixelRatio * scaleY;

    ctx.setTransform(pixelRatio, 0, 0, pixelRatio, 0, 0);
    ctx.imageSmoothingQuality = 'high';

    ctx.drawImage(
      image,
      0,
      0,
      scaleX,
      scaleY
    );

    return new Promise((resolve, reject) => {
      this.fileUrl = canvas.toBlob();
      resolve(this.fileUrl);
    });
  }

  getCroppedImg(image, crop, fileName) {
    const canvas = document.createElement('canvas');
    const pixelRatio = window.devicePixelRatio;
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    const ctx = canvas.getContext('2d');

    canvas.width = crop.width * pixelRatio * scaleX;
    canvas.height = crop.height * pixelRatio * scaleY;

    ctx.setTransform(pixelRatio, 0, 0, pixelRatio, 0, 0);
    ctx.imageSmoothingQuality = 'high';

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width * scaleX,
      crop.height * scaleY
    );

    return new Promise((resolve, reject) => {
      this.fileUrl = canvas.toBlob(() => { }, 'image/jpg', 1);
      resolve(this.fileUrl);
    });
  }

  save() {
    const { croppedImageUrl } = this.state;
    const { fileIndex } = this.props;
    this.props.upd('cropped', croppedImageUrl, fileIndex);
    this.setState({ src: croppedImageUrl })
  }

  render() {
    const { crop, croppedImageUrl } = this.state;
    const { fileIndex, file } = this.props;
    const src = file.options.cropped.length > 0 ? file.options.cropped[file.options.cropped.length - 1] : this.state.src;
    return (
      <>
        <ReactCrop
          key={this.props.aspect}
          src={src}
          crop={crop}
          rotate={0}
          ruleOfThirds
          onImageLoaded={this.onImageLoaded}
          onComplete={this.onCropComplete}
          onChange={this.onCropChange}
        />
      </>
    )
  }
}

export default Image;