import React, { useState, useCallback, useRef, useEffect, useContext } from 'react';
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css';
import { AppContext } from './main';
import { Preloader } from './loader';

function generateDownload(canvas, crop) {
  if (!crop || !canvas) {
    return;
  }

  canvas.toBlob(
    (blob) => {
      const previewUrl = window.URL.createObjectURL(blob);

      const anchor = document.createElement('a');
      anchor.download = 'cropPreview.png';
      anchor.href = URL.createObjectURL(blob);
      anchor.click();

      window.URL.revokeObjectURL(previewUrl);
    },
    'image/jpeg',
    1
  );
}

export default function ImageV2(props) {

  const { state, dispatch } = useContext(AppContext);

  const file = state.files[props.index];
  const imgRef = useRef(null);
  const [croppedImageUrl, setCroppedImageUrl] = useState(null);
  const [crop, setCrop] = useState({ unit: 'px', width: 0, height: 0, aspect: 10 / 15, x: 0, y: 0 });
  const [loading, onLoading] = useState(true);
  //const [timer, setPreComliteTimer] = useState(null);

  const onLoad = useCallback((e) => {
    const img = e.target;
    imgRef.current = img;
    let [aspect, width, height, x, y] = [
      state.files[props.index].options.aspect,
      img.width,
      img.height,
      crop.x,
      crop.y
    ];

    // Определяем ориентацию изображения
    if (width > height) {
      aspect = decodeAspect(file.options.format, true)
    }
    else {
      aspect = decodeAspect(file.options.format)
    }

    // Считаем размеры обрезки
    if (aspect > 1) {
      height = width / aspect;
      x = 0;
      y = (img.height - height) / 2;
      if (y < 0) {
        height = img.height;
        width = height * aspect;
        x = (img.width - width) / 2
        y = 0;
      }
    }
    else {
      width = height * aspect;
      x = (img.width - width) / 2;
      y = 0;
    }

    let newcrop = {
      naturalWidth: imgRef.current.width,
      naturalHeight: imgRef.current.height,
      unit: 'px',
      aspect: aspect,
      width: width,
      height: height,
      x: x,
      y: y,
    };
    onCropChange(newcrop);
    makeClientCrop(newcrop);
    onLoading(false);
    return false;
  }, []);

  const decodeAspect = (aspect, invert = false) => {
    const a = aspect.split('x');
    if (invert) {
      return a[1] / a[0];
    }
    else {
      return a[0] / a[1];
    }
  }

  const makeClientCrop = async (crop) => {

    if (imgRef && crop.width && crop.height) {
      //onLoading(true);

      const croppedImageUrl = getCroppedImg(
        imgRef.current,
        crop,
        'newFile.jpeg'
      );

      croppedImageUrl.then(res => {
        const o = file.options;
        o.preCrop = res;
        dispatch({
          type: "updateOptions", payload: {
            index: props.index,
            options: o
          }
        });
      })
    }
  }

  const getCroppedImg = (image, crop, fileName) => {
    const canvas = document.createElement('canvas');
    const pixelRatio = window.devicePixelRatio;
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    const ctx = canvas.getContext('2d');

    canvas.width = crop.width * pixelRatio * scaleX;
    canvas.height = crop.height * pixelRatio * scaleY;

    ctx.setTransform(pixelRatio, 0, 0, pixelRatio, 0, 0);
    ctx.imageSmoothingQuality = 'high';

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width * scaleX,
      crop.height * scaleY
    );

    return new Promise((resolve, reject) => {
      canvas.toBlob(
        (blob) => {
          const previewUrl = window.URL.createObjectURL(blob);
          setCroppedImageUrl(previewUrl, resolve(previewUrl));
          // window.URL.revokeObjectURL(previewUrl);
        },
        'image/jpg',
        1
      );

    });
  }

  const ImageComponent = () => {
    if (imgRef) {
      return (
        <>
          <div className="overlay" is-loaded={imgRef.current ? 1 : 0}>
            <Preloader />
          </div>
          <div className="img-wrapper">
            <img
              src={file.body}
              onLoadStart={() => onLoading(true)}
              onLoad={onLoad}
              ref={imgRef}
              className="ReactCrop__image"
              alt="" />
          </div>
        </>
      )
    }
    else {
      return null;
    }
  };

  const onCropChange = (c) => {
    c.naturalWidth = imgRef.current.width;
    c.naturalHeight = imgRef.current.height;
    dispatch({
      type: 'changeCrop',
      payload: {
        crop: c,
        index: props.index
      }
    })
    console.log('change');
  }

  useEffect(() => {
    // Если изображение горизонтальное, сразу поворачиваем рамку один раз при загрузке
    const img = imgRef.current;
    file.options.rotate = !file.options.rotate;
    if (img.width > img.height) {
      dispatch({
        type: 'changeCrop',
        payload: {
          options: file.options,
          index: props.index
        }
      })
    }
  }, [imgRef])

  useEffect(() => {
    makeClientCrop(state.files[props.index].options.crop)
    console.log('3');
  }, [state.files[props.index].options.rotate, state.files[props.index].options.cadr])

  return (
    <>
      {loading && <Preloader />}
      <ReactCrop
        crop={state.files[props.index].options.crop}
        //disabled={state.files[props.index].options.cadr} // отключаем движение рамки при условии кадрирования
        onChange={(c) => onCropChange(c)}
        onComplete={(c) => { makeClientCrop(c) }}
        renderComponent={ImageComponent()}
      />
    </>
  );
}
