import React, { useContext, useRef } from 'react';
import { AppContext } from './main';

export function GroupOptions(props) {

  const { state, dispatch } = useContext(AppContext);

  const form = useRef();

  const submit = (e) => {
    const form = e.target.closest('form');
    const data = new FormData(form);
    let formData = [];
    for (let [name, value] of data) {
      formData.push({ name: name, value: value });
    }
    dispatch({
      type: 'massSetFilesOptions',
      payload: formData
    });
  }

  if (!state.changeMod && !state.afterAddFilesMod) return null;

  return (
    <div className={`file-options-form-wrapper ${state.afterAddFilesMod ? 'popup' : ''}`}>
      <div className="card text-dark bg-light border-secondary file-options-form">
        <form ref={form}>
          <div className="card-header d-flex align-items-center justify-content-between">
            Настройки
            {state.afterAddFilesMod &&
              <button type="button" className="btn-close"></button>}
          </div>
          <div className="card-body">

            {!state.afterAddFilesMod && <ChangedFilesList />}

            {state.afterAddFilesMod &&
              <div className="alert alert-primary" role="alert">
                Пока Ваши фото загружаются, Вы можете их настроить в форме ниже.
              </div>
            }

            <div className="form-group">
              <label forhtml="exampleSelect1" className="form-label mt-4">Формат фото</label>
              <select className="form-select" name="format" id="exampleSelect1" required>
                <option disabled>Выбрать</option>
                <option value="10x15">10x15</option>
                <option value="15x20">15x20</option>
                <option value="20x30">20x30</option>
              </select>
            </div>

            <div className="form-group">
              <label forhtml="exampleSelect2" className="form-label mt-4">Тип бумаги</label>
              <select className="form-select" name="paper" id="exampleSelect2" required>
                <option disabled>Выбрать</option>
                <option value="матовая">Матовая</option>
                <option value="глянец">Глянец</option>
              </select>
            </div>

            <div className="form-group mt-3">
              <div className="form-check form-switch">
                <label className="form-check-label" forhtml="flexSwitchCheckDefault">Кадрирование</label>
                <input className="form-check-input" name="cadr" type="checkbox" role="switch" id="flexSwitchCheckDefault" />
              </div>
            </div>

            <div className="mt-3">
              <label forhtml="exampleFormControlInput3" className="form-label">Количество</label>
              <input type="number" name="count" className="form-control" id="exampleFormControlInput3" defaultValue="1" />
            </div>

          </div>
          <div className="card-footer d-flex justify-content-end">
            <div onClick={submit} className="btn btn-success">Применить настройки</div>
          </div>
        </form>
      </div>
    </div>
  )
}

function ChangedFilesList(props) {
  const { state, dispatch } = useContext(AppContext);

  let list = null;
  if (state.files) {
    list = state.files.map((n) => {
      if (n && n.options && n.options.changed) {
        return (
          <div key={n.id} className="changed-files-list-item"
            style={{ backgroundImage: `url(${window.URL.createObjectURL(n.file)})` }}></div>
        )
      }
      return false;
    })

    if (list.length > 0)
      return <div className="changed-file-list">{list}</div>;
    else
      return null;
  }
  else
    return null;

}