import { useContext, useState } from 'react';
import { AppContext } from './main'
import LineIcon from "react-lineicons";
import { ItemOptionListBtn } from './list';

export function DisplayOptionList(props) {
  const { state, dispatch } = useContext(AppContext);
  const [nav, setNav] = useState('default');
  const o = props.o;
  const [count, changeCount] = useState(o.count);

  switch (nav) {
    case "default":
      return (
        <>
          <div onClick={() => { setNav("formats") }} className="item-option item-option--linear o format"><LineIcon name="lni lni-ruler-alt" />&nbsp;{o.format ? o.format : 'Нет'}</div>
          <div onClick={() => { setNav("paper") }} className="item-option item-option--linear o paper"><LineIcon name="lni lni-write" />&nbsp;{o.paper ? o.paper : 'Нет'}</div>
          <div onClick={() => { setNav("crop") }} className="item-option item-option--linear o crop"><LineIcon name="lni lni-crop" />&nbsp;{o.cadr ? 'Да' : 'Нет'}</div>
          <div onClick={() => { setNav("count") }} className="item-option item-option--linear o count"><LineIcon name="lni lni-shopping-basket" />&nbsp;{o.count ? o.count : 'Нет'}</div>
        </>
      )
    case "formats":
      let list = state.formatList.map((f, i) => {
        return (
          <ChangeOptionBtn action="changeFormat" mod="" f={f} key={i} nav={setNav} index={props.index} />
        )
      })
      list.push(<ChangeOptionBtn
        f={'назад'}
        key={'default'}
        nav={setNav}
        index={'back'} />);
      return list;

    case "paper":
      const papers = state.paperList.map((f, i) => {
        return (
          <ChangeOptionBtn action="changePaper" mod="" f={f} key={i} nav={setNav} index={props.index} />
        )
      })
      papers.push(<ChangeOptionBtn
        f={'назад'}
        key={'default'}
        nav={setNav}
        mod="to-back"
        index={'back'} />);
      return papers;

    case "crop":
      return (
        <>
          <ChangeOptionBtn
            action="changeCadr"
            mod=""
            f={false}
            label="Нет"
            key="n"
            nav={setNav} index={props.index} />
          <ChangeOptionBtn
            action="changeCadr"
            mod=""
            f={true}
            label="Да"
            key="y"
            nav={setNav} index={props.index} />
          <ChangeOptionBtn
            f={'назад'}
            key={'default'}
            nav={setNav}
            mod="to-back"
            index={'back'} />
        </>
      )

    case "count":
      const changeCount = (n) => {
        let k = -1;
        if (n) k = 1;
        dispatch({
          type: 'changeCount',
          payload: {
            index: props.index,
            format: +o.count + k
          }
        })
      }
      return (
        <>
          <div className="item-count">{o.count}</div>
          <div className="d-flex justify-content-between">
            <div onClick={() => changeCount()} className="item-option">
              <LineIcon name="minus" />
            </div>
            <div onClick={() => changeCount(true)} className="item-option">
              <LineIcon name="plus" />
            </div>
          </div>

          <ChangeOptionBtn
            f={'назад'}
            key={'default'}
            nav={setNav}
            mod="to-back"
            index={'back'} />
        </>
      )

    default:
      return null;
  }
}

function ChangeOptionBtn(props) {
  const { state, dispatch } = useContext(AppContext);
  const label = typeof (props.f) === 'string' ? props.f : props.label;
  return (
    <button
      onClick={() => {
        if (props.index !== 'back') {
          dispatch({
            type: props.action,
            payload: {
              index: props.index,
              format: props.f
            }
          })
        }
        props.nav("default");
      }}
      className={`item-option item-option--linear ${props.mod}`}>{label}</button>
  )
}