import React from "react";

export function Preloader() {
    return (
        <svg className="loader" version="1.1" id="L9" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px"
            viewBox="0 0 30 30" enableBackground="new 0 0 0 0">
            <rect x="0" y="0" width="4" height="10" fill="#fff">
                <animateTransform attributeType="xml"
                    attributeName="transform" type="translate"
                    values="0 0; 0 20; 0 0"
                    begin="0" dur="0.6s" repeatCount="indefinite" />
            </rect>
            <rect x="10" y="0" width="4" height="10" fill="#f0a">
                <animateTransform attributeType="xml"
                    attributeName="transform" type="translate"
                    values="0 0; 0 20; 0 0"
                    begin="0.2s" dur="0.6s" repeatCount="indefinite" />
            </rect>
            <rect x="20" y="0" width="4" height="10" fill="#fff">
                <animateTransform attributeType="xml"
                    attributeName="transform" type="translate"
                    values="0 0; 0 20; 0 0"
                    begin="0.4s" dur="0.6s" repeatCount="indefinite" />
            </rect>
        </svg>
    )
}