import React, { useState, PureComponent } from "react";
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css';
import { ItemOptionListBtn } from "./list";


export class SetupImagePopup extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      src: '',
      crop: {
        unit: '%',
        width: '100',
        aspect: 2 / 3
      }
    }
    this.save = this.save.bind(this);
  }

  componentDidMount() {
    const f = this.props.file;
    const src = f.options.cropped ? f.options.cropped : window.URL.createObjectURL(this.props.file.file);
    this.setState({ src: src });
  }

  componentWillUnmount() {
    const f = this.props.file;
    const src = f.options.cropped ? f.options.cropped : window.URL.createObjectURL(this.props.file.file);
    this.setState({ src: src });
  }

  onImageLoaded = (image) => {
    this.imageRef = image;
  };

  onCropComplete = (crop) => {
    this.makeClientCrop(crop);
  };

  onCropChange = (crop, percentCrop) => {
    // You could also use percentCrop:
    // this.setState({ crop: percentCrop });
    this.setState({ crop });
  };

  async makeClientCrop(crop) {
    if (this.imageRef && crop.width && crop.height) {
      const croppedImageUrl = await this.getCroppedImg(
        this.imageRef,
        crop,
        'newFile.jpeg'
      );
      this.setState({ croppedImageUrl });
    }
  }

  getCroppedImg(image, crop, fileName) {
    const canvas = document.createElement('canvas');
    const pixelRatio = window.devicePixelRatio;
    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;
    const ctx = canvas.getContext('2d');

    canvas.width = crop.width * pixelRatio * scaleX;
    canvas.height = crop.height * pixelRatio * scaleY;

    ctx.setTransform(pixelRatio, 0, 0, pixelRatio, 0, 0);
    ctx.imageSmoothingQuality = 'high';

    ctx.drawImage(
      image,
      crop.x * scaleX,
      crop.y * scaleY,
      crop.width * scaleX,
      crop.height * scaleY,
      0,
      0,
      crop.width * scaleX,
      crop.height * scaleY
    );

    return new Promise((resolve, reject) => {
      this.fileUrl = canvas.toDataURL();
      resolve(this.fileUrl);
    });
  }

  close() {
    this.props.setSetupImageMod(null);
  }

  save() {
    const { croppedImageUrl } = this.state;
    const { fileIndex } = this.props;
    this.props.upd('cropped', croppedImageUrl, fileIndex);
    this.setState({ src: croppedImageUrl })
  }

  render() {
    const { crop, src, croppedImageUrl } = this.state;
    const { fileIndex, file } = this.props;
    return (
      <div className="modal fade show" id="file-direction" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="false">
        <div className="modal-dialog" role="document">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="file-direction-label">Управление файлом</h5>
              <button onClick={this.props.closeModal} type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div className="modal-body">
              <div className="container-fluid">
                <div className="row">
                  <div className="col-md-8 react-crop-wrapper-row">
                    <div className="react-crop-wrapper">
                      {src && (
                        <>
                          {/* Скрыто стилями */}
                          <div className="item-option-list">
                            <ItemOptionListBtn
                              actionFunction={() => this.props.upd('rotate', !file.options.rotate, fileIndex)}
                              iconClass="lni lni-ruler-alt"
                              itemClass="rotate"
                              index={this.props.fileIndex}
                            />
                            <ItemOptionListBtn
                              actionFunction={() => this.props.upd('rotate', !file.options.rotate, fileIndex)}
                              iconClass="lni lni-spinner-arrow"
                              itemClass="rotate"
                              index={this.props.fileIndex}
                            />
                          </div>
                          <ReactCrop
                            src={src}
                            crop={crop}
                            ruleOfThirds
                            onImageLoaded={this.onImageLoaded}
                            onComplete={this.onCropComplete}
                            onChange={this.onCropChange}
                          />

                        </>
                      )}
                    </div>
                  </div>
                  <div className="col-md-4 react-crop-options-bar">
                    {src && (
                      <div className="file-direction__crop">
                        <span>Предварительный просмотр</span>
                        <img src={croppedImageUrl} alt="" />
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>

            <div className="modal-footer">
              <button onClick={this.props.closeModal} type="button" className="btn btn-secondary" data-dismiss="modal">Закрыть</button>
              <button onClick={this.save} type="button" className="btn btn-primary">Обрезать</button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}