import React, { createContext, useContext, useReducer } from "react";
import ImageList from "./list";
import { GroupOptions } from "./file_options_form.jsx";
import { SetupImagePopup } from "./setup-image-popup";
import { reducer } from "./reducer";
import { Cart } from "./cart";

export let AppContext = createContext(null);

export function Main() {

  const [state, dispatch] = useReducer(reducer, {
    files: [],
    formatList: ["10x15", "15x20", "20x30"],
    paperList: ["глянец", "матовый"],
    changeMod: false,
    setupImageMod: null,
    lastModifiedTime: new Date().getTime(),
    afterAddFilesMod: false
  });

  return (
    <AppContext.Provider value={{ state, dispatch }}>
      <Group />

      {typeof setupImageMod === 'number' &&
        <SetupImagePopup
          fileIndex={state.setupImageMod}
        />}
    </AppContext.Provider>
  )

}

function Group(props) {

  const { state, dispatch } = useContext(AppContext);

  const addFiles = (e) => {
    dispatch({ type: "add", payload: e.target.files });
  }

  return (
    <div className="row">
      <div className={`col-12 col-lg-${state.changeMod ? 10 : 12}`}>
        <div className="card border-secondary my-3">
          <div className="card-header d-flex justify-content-between align-items-center">
            Ваши фотографии: {state.files.length}
            <MassSetOptionsBtn
              modOn={state.changeMod}
              setChangingMod={state.setChangingMod} />
          </div>
          <div className="card-body">
            <div className="image-list">
              <label>
                <span>Добавить фото</span>
                <input onChange={addFiles} className="form-control" type="file" multiple />
              </label>
              <ImageList />
            </div>

            <Cart />

          </div>
          <div className="card-footer d-flex justify-content-end">
            <div className="btn btn-success disabled">Перейти к оформлению заказа</div>
          </div>
        </div>
      </div>
      <div className="col-12 col-lg-2">
        <GroupOptions />
      </div>
    </div>
  )
}

function MassSetOptionsBtn(props) {

  const { state, dispatch } = useContext(AppContext);

  let btnText = 'Выбрать несколько';
  if (state.changeMod) {
    btnText = 'отменить';
  }

  const modded = () => {
    dispatch({
      type: 'setChangingMod'
    })
  }

  return (
    <div onClick={modded} className="btn btn-info text-white">{btnText}</div>
  )
}